html, body {
  max-width: 100%;
  overflow-x: hidden; }

* {
  margin: 0;
  padding: 0; }

.focusedLink {
  border-bottom: 1px solid #000; }

.about_headerBlock_wrapper_hamburgerIcon {
  display: none; }

.about_hamburger {
  display: none;
  background-color: #FAC91B;
  min-height: 100vh;
  padding: 0 20px; }
  .about_hamburger_link {
    display: block;
    font-family: MontserratBold;
    font-size: 36px;
    color: black; }

.show {
  display: block; }

.hide {
  display: none; }

.about_headerBlock {
  background-color: #FAC91B; }
  .about_headerBlock_wrapper {
    max-width: 1350px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 20px; }
    .about_headerBlock_wrapper_left {
      display: flex;
      align-items: center; }
      .about_headerBlock_wrapper_left_logo {
        background-color: black;
        width: 70px;
        height: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        font-family: VerdanaRegular;
        font-size: 36px; }
      .about_headerBlock_wrapper_left_name {
        font-family: VerdanaRegular;
        font-size: 15px;
        margin-left: 25px;
        font-weight: 500;
        color: black; }
    .about_headerBlock_wrapper_right {
      display: flex;
      justify-content: space-between;
      align-items: center; }
      .about_headerBlock_wrapper_right_navBlock {
        display: flex;
        justify-content: space-between; }
        .about_headerBlock_wrapper_right_navBlock_link {
          font-family: VerdanaRegular;
          font-size: 15px;
          margin-left: 50px;
          color: black; }
      .about_headerBlock_wrapper_right_cart {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-left: 85px; }
        .about_headerBlock_wrapper_right_cart_iconBlock {
          width: 50px;
          height: 50px;
          border-radius: 50%;
          background-color: black;
          display: flex;
          justify-content: center;
          align-items: center;
          margin-right: 40px; }
        .about_headerBlock_wrapper_right_cart_text {
          font-family: VerdanaRegular;
          font-size: 15px; }
        .about_headerBlock_wrapper_right_cart_traingle {
          width: 0;
          height: 0;
          border-left: 8px solid transparent;
          border-right: 8px solid transparent;
          border-top: 16px solid black;
          margin-left: 10px; }

.about_textBlock {
  width: 100%; }
  .about_textBlock_wrapper {
    max-width: 1350px;
    width: 100%;
    margin: 0 auto;
    padding-top: 70px; }
    .about_textBlock_wrapper_text {
      font-family: MontserratRegular;
      font-size: 30px;
      color: black; }

@media screen and (max-width: 1350px) {
  .about_headerBlock_wrapper {
    padding-left: 20px;
    padding-right: 20px; }
  .about_textBlock {
    padding-left: 20px;
    padding-right: 20px; } }

@media screen and (max-width: 950px) {
  .about_headerBlock_wrapper_right {
    display: none; }
  .about_headerBlock_wrapper_hamburgerIcon {
    display: block; } }

@media screen and (max-width: 950px) {
  .about_textBlock_wrapper_text {
    font-size: 20px; } }
