html, body {
  max-width: 100%;
  overflow-x: hidden; }

* {
  margin: 0;
  padding: 0; }

.focusedLink {
  border-bottom: 1px solid #000; }

.friends_headerBlock_wrapper_hamburgerIcon {
  display: none; }

.friends_hamburger {
  display: none;
  background-color: #FAC91B;
  min-height: 100vh;
  padding: 0 20px; }
  .friends_hamburger_link {
    display: block;
    font-family: MontserratBold;
    font-size: 36px;
    color: black; }

.show {
  display: block; }

.hide {
  display: none; }

.friends_headerBlock {
  background-color: #FAC91B; }
  .friends_headerBlock_wrapper {
    max-width: 1350px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 20px; }
    .friends_headerBlock_wrapper_left {
      display: flex;
      align-items: center; }
      .friends_headerBlock_wrapper_left_logo {
        background-color: black;
        width: 70px;
        height: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        font-family: VerdanaRegular;
        font-size: 36px; }
      .friends_headerBlock_wrapper_left_name {
        font-family: VerdanaRegular;
        font-size: 15px;
        margin-left: 25px;
        font-weight: 500;
        color: black; }
    .friends_headerBlock_wrapper_right {
      display: flex;
      justify-content: space-between;
      align-items: center; }
      .friends_headerBlock_wrapper_right_navBlock {
        display: flex;
        justify-content: space-between; }
        .friends_headerBlock_wrapper_right_navBlock_link {
          font-family: VerdanaRegular;
          font-size: 15px;
          margin-left: 50px;
          color: black; }
      .friends_headerBlock_wrapper_right_cart {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-left: 85px; }
        .friends_headerBlock_wrapper_right_cart_iconBlock {
          width: 50px;
          height: 50px;
          border-radius: 50%;
          background-color: black;
          display: flex;
          justify-content: center;
          align-items: center;
          margin-right: 40px; }
        .friends_headerBlock_wrapper_right_cart_text {
          font-family: VerdanaRegular;
          font-size: 15px; }
        .friends_headerBlock_wrapper_right_cart_traingle {
          width: 0;
          height: 0;
          border-left: 8px solid transparent;
          border-right: 8px solid transparent;
          border-top: 16px solid black;
          margin-left: 10px; }

.friends_contentBlock {
  width: 100%; }
  .friends_contentBlock_wrapper {
    max-width: 1350px;
    width: 100%;
    margin: 0 auto;
    padding-top: 70px; }
    .friends_contentBlock_wrapper_name {
      font-family: MontserratRegular;
      font-size: 36px; }
    .friends_contentBlock_wrapper_itemsBlock {
      display: grid;
      grid-template-columns: 30% 30% 30%;
      grid-column-gap: 5.2%;
      grid-row-gap: 80px;
      margin-top: 60px; }
      .friends_contentBlock_wrapper_itemsBlock_item {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #A6A6A6;
        height: 250px; }
        .friends_contentBlock_wrapper_itemsBlock_item_itemText {
          font-family: MontserratRegular;
          font-size: 72px; }

.friends_footerBlock {
  width: 100%;
  padding-top: 200px;
  padding-bottom: 50px; }
  .friends_footerBlock_wrapper {
    max-width: 1350px;
    width: 100%;
    margin: 0 auto; }
    .friends_footerBlock_wrapper_text {
      font-family: MontserratRegular;
      font-size: 30px;
      text-align: justify; }

@media screen and (max-width: 1350px) {
  .friends_headerBlock_wrapper {
    padding-left: 20px;
    padding-right: 20px; }
  .friends_contentBlock_wrapper {
    padding-left: 20px;
    padding-right: 20px; }
  .friends_footerBlock_wrapper {
    padding-left: 20px;
    padding-right: 20px; } }

@media screen and (max-width: 1000px) {
  .friends_contentBlock_wrapper_itemsBlock {
    grid-template-columns: 45% 45%;
    grid-column-gap: 10%; } }

@media screen and (max-width: 950px) {
  .friends_headerBlock_wrapper_right {
    display: none; }
  .friends_headerBlock_wrapper_hamburgerIcon {
    display: block; }
  .friends_footerBlock {
    padding-top: 100px; } }

@media screen and (max-width: 715px) {
  .friends_contentBlock_wrapper_itemsBlock {
    grid-template-columns: auto;
    grid-column-gap: 0%; }
  .friends_footerBlock_wrapper_text {
    font-size: 15px; }
  .friends_footerBlock {
    padding-top: 50px; } }

@media screen and (max-width: 450px) {
  .friends_contentBlock_wrapper_itemsBlock_item {
    height: 175px; }
  .friends_contentBlock_wrapper_itemsBlock_item_itemText {
    font-size: 45px; } }

@media screen and (max-width: 400px) {
  .friends_contentBlock_wrapper_name {
    font-size: 24px; } }
